import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import SiteCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/companyMaster/site/actions";
import { Box } from "../../../App/app.style";

const redirectTo = '/dashboard/companies/sites'

class CreateSite extends Component {
  componentDidUpdate() {
    // Display error notif
    if (this.props.site.error) {
      notification("error", this.props.site.error);
      this.props.clearError();
    }

    if (this.props.site.success && !this.props.site.loading) {
      this.props.history.replace(redirectTo);
    }
  }
  handleSubmit = value => {
    const { storeSite } = this.props;

    storeSite(value);
  };

  render() {
    // console.log(this.props);

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="sites.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <SiteCreateForm auth={this.props.auth} submit={this.handleSubmit} site={this.props.site} />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, site: state.site };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateSite);
