import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Checkbox, Col } from "antd";

//const { Option } = Select;
const FormItem = Form.Item;
const InputGroup = Input.Group;

class SiteCreateForm extends Component {
  
  state = {
    check_code:false
  }
  
  handleChangeCode = () => {
    const {setFieldsValue} = this.props.form
    this.setState({check_code:!this.state.check_code})
    if(this.state.check_code===false){
      setFieldsValue({
        ["site_code"]: null,
      })
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    
    this.props.form.validateFieldsAndScroll((err, values) => {
      //console.log(err)
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    //console.log(this.props.site);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { site } = this.props;
    const { loading } = site;
    //const data = site.site ? site.site : [];
    //const used = data.is_used;
    const error = site.errorField ? site.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <InputGroup compact>
              <Col span={20}>  
                <FormItem
                  {...formItemLayout}
                  label={<IntlMessages id="sites.add.code" />}
                  validateStatus={
                    error.site_code || getFieldError("site_code") ? "error" : null
                  }
                  help={
                    error.site_code ? error.site_code : getFieldError("site_code")
                  }
                >
                  {getFieldDecorator("site_code", {
                      rules: [
                      {
                        max: 30,
                        message: <IntlMessages id="maxLength30" />
                      }
                    ]
                  })(
                    <Input
                      disabled={this.state.check_code ? true : false}
                      //style={{ textTransform: "uppercase" }}
                    />
                  )}
                </FormItem>
              </Col>
              <Col span={4}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="autoGenerateCode" />}
                >
                  <Checkbox
                    style={{  
                      paddingTop:"5px",
                      paddingLeft: "8px",
                    }}
                    checked={this.state.check_code}
                    onChange={this.handleChangeCode}
                  />
                  
                </Form.Item>
              </Col>
            </InputGroup>
            

            <FormItem
              {...formItemLayout}
              label={<IntlMessages id="sites.add.name" />}
            >
              {getFieldDecorator("site_name", {
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(
                <Input
                //disabled={used ? true : false}
                />
              )}
            </FormItem>
          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedSiteCreateForm = Form.create({ name: "create_site" })(
  SiteCreateForm
);

export default WrappedSiteCreateForm;
